<template>
  <b-container id="projects">
    <h1>Projects</h1>
    <hr>
    <div>
      <b-row class="justify-content-center" v-if="repos.length !== 0" >
        <b-card-group deck class="col-md-4" v-for="data in repos" :key="data.id">
          <b-card
              v-bind:title="data.name"
              style="margin-bottom:20px;"
          >
            <b-card-text>{{ `${data.description}` }}</b-card-text>
            <template #footer>
              <b-button :href="data.svn_url" target="_blank" variant="primary">View Repo</b-button>
            </template>
          </b-card>
        </b-card-group>
      </b-row>
      <b-row v-else class="justify-content-center">
        <h5>No Repos available yet 😢 </h5>
      </b-row>
    </div>
  </b-container>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      repos: []
    };
  },
  mounted() {
  axios({
      method: "get",
      url: "https://api.github.com/users/LittleDeltaPlus/repos"
        }
    ).then(response => {
      this.repos = response.data;
      this.repos.splice(this.repos.findIndex(repo => repo.id===283507662),1);
      console.log(`got ${this.repos[0].name}`);
    }).catch(e => {
      console.error(e);
    })
  }
};
</script>

